.mp-admin_tab.styled-2 .RRT__tab--selected {
  position: relative;
  color: white;
}
.mp-admin_tab.styled-2 .RRT__tab--selected::before {
  content: " ";
  background: #033c73a3;
  color: white;
  border-radius: 5px;
  position: absolute;
  width: 100%;
  left: 0;
  top: 0.55rem;
  bottom: 0.5rem;
  z-index: -1;
}

.mp-admin_tab.styled-2 .RRT__showmore .RRT__tab--selected::before {
  background: transparent;
  color: #033c73a3;
}

/* tab ben trong tab */
.RRT__panel > .mp-admin_tab {
  margin-top: -1rem;
}
